import React, {
    Component
} from 'react';

import {
    add_image_icon_svg
} from './../../images';

import ContentPage from './../Containers/ContentPage';

import './../../styles/organisationDetails.scss';

class OrganisationDetails extends Component {
    render() {
        return (
            <ContentPage pageTitle="Nursing Home Limited">
                <div className="main_part_header">
                    <div></div>
                    <div>
                        <button className="btn btn-primary mr10">Edit</button>
                        <button className="btn btn-primary">Delete</button>
                    </div>
                </div>
                <div className="dashboard">
                    <div className="dashboard_item dashboard_item-md-4">
                        <div className="card org_info">
                            <a href="/" className="org_image">
                                <img alt="" src={add_image_icon_svg} />
                            </a>
                            <h3>Organistion Name</h3>
                            <h3 className="org_name">Nursing Home Limited</h3>
                            <table>
                                <tbody>
                                    <tr>
                                        <td className="pb10">Telephone</td>
                                        <td className="pb10">0161 234 5678</td>
                                    </tr>
                                
                                    <tr>
                                        <td>Address Line 1</td>
                                        <td>-----</td>
                                    </tr>
                                    <tr>
                                        <td>Address Line 2</td>
                                        <td>-----</td>
                                    </tr>
                                    <tr>
                                        <td>Town/City</td>
                                        <td>-----</td>
                                    </tr>
                                    <tr>
                                        <td>County</td>
                                        <td>-----</td>
                                    </tr>
                                    <tr>
                                        <td>Postcode</td>
                                        <td>-----</td>
                                    </tr>
                                    <tr>
                                        <td className="pb10">Country</td>
                                        <td className="pb10">-----</td>
                                    </tr>
                                    <tr>
                                        <td className="pb10">Organisation Type</td>
                                        <td className="pb10">Hospital</td>
                                    </tr>
                                    <tr>
                                        <td>Email</td>
                                        <td>hospital@email.com</td>
                                    </tr>
                                </tbody>
                            </table>
                        </div>
                    </div>
                    <div className="dashboard_item">
                        <div className="card stat">
                            <strong>123</strong> Organisations
                        </div>
                    </div>
                    <div className="dashboard_item">
                        <div className="card stat">
                            <strong>10</strong> Patients
                        </div>
                    </div>
                    <div className="dashboard_item">
                        <div className="card stat">
                            <strong>6</strong> Data Log
                        </div>
                    </div>
                    <div className="dashboard_item dashboard_item-md-4">
                        <div className="card admin_info">
                            <p>Admin for Organistion</p>
                            <table>
                                <tbody>
                                    <tr>
                                        <td>First Name</td>
                                        <td>Tom</td>
                                    </tr>
                                    <tr>
                                        <td>Last Name</td>
                                        <td>Tom</td>
                                    </tr>
                                    <tr>
                                        <td>Email</td>
                                        <td>name@email.com</td>
                                    </tr>
                                </tbody>
                            </table>
                        </div>
                    </div>
                </div>
            </ContentPage>
        );
    }
}

export default OrganisationDetails;