import React, { Component } from 'react';
import {
    Link
  } from "react-router-dom";

class Links extends Component {
    render() {
        return (
            <div style={{margin: 50}}>
              <Link to="/login">login</Link><br />
              <Link to="/restPassword">restPassword</Link><br />
              <Link to="/restPasswordDone">restPasswordDone</Link><br />
              <Link to="/newPassword">newPassword</Link><br />
              <Link to="/newPasswordDone">newPasswordDone</Link><br />
              <Link to="/dashboard">dashboard</Link><br />
              <Link to="/organisation">organisation</Link><br />
              <Link to="/organisationDetails">organisationDetails</Link><br />
              <Link to="/patients">patients</Link><br />
              <Link to="/staff">staff</Link><br />
              <Link to="/settingTerms">settingTerms</Link><br />
              <Link to="/profile">profile</Link><br />
              <Link to="/patientDetails">patientDetails + Modal</Link><br />
            </div>
        );
    }
}

export default Links;