import React, {
    Component
} from 'react';

import ContentPage from './../Containers/ContentPage';

class Profile extends Component {
    render() {
        return (
            <ContentPage pageTitle="Profile">
                <div className="dashboard">
                    <div className="dashboard_item dashboard_item-full">
                        <div className="card">
                            <div className="main_part_header">
                                <div>
                                    <h2>Mike Smith <span>- [Super Admin]</span></h2>
                                </div>
                                <div>
                                    <button className="btn btn-primary">Edit</button>
                                </div>
                            </div>
                            <div className="content terms-settings">
                                <div className="setting-col">
                                    <div className="block">
                                        <p>First Name</p>
                                        <h3>Mike</h3>
                                    </div>
                                    <div className="block">
                                        <p>Last Name</p>
                                        <h3>Smith</h3>
                                    </div>
                                    <div className="block">
                                        <p>Administration Level</p>
                                        <h3>Super Admin</h3>
                                    </div>
                                </div>
                                <div className="setting-col">
                                    <div className="block">
                                        <p>Email Address</p>
                                        <h3>mike.smith@gmail.com</h3>
                                    </div>
                                    <div className="block">
                                        <p>Password</p>
                                        <h3>********</h3>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </ContentPage>
        );
    }
}

export default Profile;