import React, {
    Component
} from 'react';

import './../../../styles/login.scss';
import {
    logo_splash_png,
} from './../../../images';

import {Link} from 'react-router-dom';

class NewPassword extends Component {
    render() {
        return (
            <div className="main">
                <form className="splash-form">
                    <div className="logo-part">
                        <img src={logo_splash_png} alt="Liscgo" />
                    </div>
                    <div className="form-part">
                        <h3>Create New Password</h3>
                        <input id="newPassword" autoComplete="false" type="password" placeholder="New Password" />
                        <input id="confirmNewPassword" autoComplete="false" type="password" placeholder="Confirm New Password" />
                        <hr />
                        <Link to="/newPasswordDone" className="btn btn-primary btn-block">Reset Password</Link>                    
                    </div>
                </form>
            </div>
        );
    }
}

export default NewPassword;