import React, {
    Component
} from 'react';

import ContentPage from './../Containers/ContentPage';

import {
    button_plus_svg,
    search_svg
} from './../../images';

class Staff extends Component {
    render() {
        return (
            <ContentPage pageTitle="Staff">
                <div className="dashboard">
                    <div className="dashboard_item dashboard_item-full">
                        <div className="card">
                            <div className="main_part_header">
                                <div>
                                    <button className="btn btn-primary">Add Staff
                                        <img src={button_plus_svg} alt="" />
                                    </button>
                                </div>
                                <div className="search-box">
                                    <input type="text" className="search-input" />
                                    <img src={search_svg} alt="" className="search-icon" />
                                </div>
                            </div>
                            <div className="content">

                                <div className="table-responsive-md">
                                    <table className="table">
                                        <thead className="thead-light">
                                        <tr>
                                            <th scope="col">First Name</th>
                                            <th scope="col">Last Name</th>
                                            <th scope="col">Permissions</th>
                                            <th scope="col">Status</th>
                                            <th scope="col"></th>
                                            <th scope="col">Action</th>
                                        </tr>
                                        </thead>
                                        <tbody>
                                        <tr>
                                            <td>First Name</td>
                                            <td>Last Name</td>
                                            <td>Organisation Admin</td>
                                            <td>Pending</td>
                                            <td><a href="/" className="btn btn-primary">View</a></td>
                                            <td className="action-td dropdown">
                                                <a href="/">•••</a>

                                                <div className="dropdown-menu">
                                                    <a className="dropdown-item" href="/">Edit</a>
                                                    <a className="dropdown-item" href="/">Delete</a>
                                                </div>
                                            </td>
                                        </tr>
                                        <tr>
                                            <td>First Name</td>
                                            <td>Last Name</td>
                                            <td>Organisation Admin</td>
                                            <td>Pending</td>
                                            <td><a href="/" className="btn btn-primary">View</a></td>
                                            <td className="action-td dropdown">
                                                <a href="/">•••</a>

                                                <div className="dropdown-menu">
                                                    <a className="dropdown-item" href="/">Edit</a>
                                                    <a className="dropdown-item" href="/">Delete</a>
                                                </div>
                                            </td>
                                        </tr>
                                        <tr>
                                            <td>First Name</td>
                                            <td>Last Name</td>
                                            <td>Organisation Admin</td>
                                            <td>Pending</td>
                                            <td><a href="/" className="btn btn-primary">View</a></td>
                                            <td className="action-td dropdown">
                                                <a href="/">•••</a>
                                                <div className="dropdown-menu">
                                                    <a className="dropdown-item" href="/">Edit</a>
                                                    <a className="dropdown-item" href="/">Delete</a>
                                                </div>
                                            </td>
                                        </tr>
                                        </tbody>
                                    </table>
                                </div>

                                <nav>
                                    <ul className="pagination">
                                        <li className="page-item disabled">
                                            <a className="page-link" href="/" tabIndex="-1">Previous</a>
                                        </li>
                                        <li className="page-item page-number disabled active">
                                            <a className="page-link" href="/">1</a>
                                        </li>

                                        <li className="page-item page-number disabled">
                                            <a className="page-link" href="/">2</a>
                                        </li>

                                        <li className="page-item page-number disabled">
                                            <a className="page-link" href="/">3</a>
                                        </li>

                                        <li className="page-item disabled">
                                            <a className="page-link" href="/">Next</a>
                                        </li>
                                    </ul>
                                </nav>
                            </div>
                        </div>
                    </div>
                </div>
            </ContentPage>
        );
    }
}

export default Staff;