import React, {
    Component
} from 'react';
import {
    Link
} from 'react-router-dom';

import ContentPage from './../Containers/ContentPage';

import {
    button_plus_svg,
    search_svg
} from './../../images';

class Organisation extends Component {
    render() {
        return (
            <ContentPage pageTitle="Organisation">
                <div className="dashboard">
                    <div className="dashboard_item dashboard_item-full">
                        <div className="card">
                            <div className="main_part_header">
                                <div>
                                    <Link to="/organisationDetails" className="btn btn-primary">Add Organisation
                                        <img src={button_plus_svg} alt="" />
                                    </Link>
                                </div>
                                <div className="search-box">
                                    <input type="text" className="search-input" />
                                    <img src={search_svg} alt="" className="search-icon" />
                                </div>
                            </div>
                            <div className="content">
                                <div className="table-responsive-md">
                                    <table className="table">
                                        <thead className="thead-light">
                                        <tr>
                                            <th scope="col">Date Registered</th>
                                            <th scope="col">Organisation Name</th>
                                            <th scope="col">Telephone</th>
                                            <th scope="col">Telephone</th>
                                            <th scope="col">Status</th>
                                            <th scope="col"></th>
                                        </tr>
                                        </thead>
                                        <tbody>
                                        <tr>
                                            <td>dd/mm/yy</td>
                                            <td><Link to="/organisationDetails">Nursing Home Limited</Link></td>
                                            <td>0161 123 4567</td>
                                            <td>Manchester</td>
                                            <td>Pending</td>
                                            <td><Link to="/organisationDetails" className="btn btn-primary">View</Link></td>
                                        </tr>
                                        <tr>
                                            <td>dd/mm/yy</td>
                                            <td><Link to="/organisationDetails">Nursing Home Limited</Link></td>
                                            <td>0161 123 4567</td>
                                            <td>Manchester</td>
                                            <td>Pending</td>
                                            <td><Link to="/organisationDetails" className="btn btn-primary">View</Link></td>
                                        </tr>
                                        <tr>
                                            <td>dd/mm/yy</td>
                                            <td><Link to="/organisationDetails">Nursing Home Limited</Link></td>
                                            <td>0161 123 4567</td>
                                            <td>Manchester</td>
                                            <td>Pending</td>
                                            <td><Link to="/organisationDetails" className="btn btn-primary">View</Link></td>
                                        </tr>
                                        </tbody>
                                    </table>
                                </div>
                                <nav>
                                    <ul className="pagination">
                                        <li className="page-item disabled">
                                            <a className="page-link" href="/" tabIndex="-1">Previous</a>
                                        </li>
                                        <li className="page-item page-number disabled active">
                                            <a className="page-link" href="/">1</a>
                                        </li>
                                        <li className="page-item page-number disabled">
                                            <a className="page-link" href="/">2</a>
                                        </li>
                                        <li className="page-item page-number disabled">
                                            <a className="page-link" href="/">3</a>
                                        </li>
                                        <li className="page-item disabled">
                                            <a className="page-link" href="/">Next</a>
                                        </li>
                                    </ul>
                                </nav>
                            </div>
                        </div>
                    </div>
                </div>
            </ContentPage>
        );
    }
}

export default Organisation;