import React, {
    Component
} from 'react';

import './../../../styles/login.scss';
import {
    logo_splash_png
} from './../../../images';

import {Link} from 'react-router-dom';

class RestPasswordDone extends Component {
    render() {
        return (
            <div className="main">
                <form className="splash-form">
                    <div className="logo-part">
                        <img src={logo_splash_png} alt="Liscgo" />
                    </div>
                    <div className="form-part">
                    <h3>Reset Password</h3>
                        <p>Thank you. A reset password link has been sent to [insert user’s email]. Please check your junk/spam folder.</p>
                        <input id="userName" type="email" placeholder="Email Address" className="visibility-hidden" />
                        <Link to="/newPassword" className="btn btn-primary btn-block">OK</Link>
                    </div>
                </form>
            </div>
        );
    }
}

export default RestPasswordDone;