import React, {
    Component
} from 'react';

import './../../../styles/login.scss';
import {
    logo_splash_png
} from './../../../images';

import {Link} from 'react-router-dom';

class Login extends Component {
    render() {
        return (
            <div className="main">
                <form className="splash-form">
                    <div className="logo-part">
                        <img src={logo_splash_png} alt="Liscgo" />
                    </div>
                    <div className="form-part">
                        <input id="userName" type="text" placeholder="Username" />
                        <input id="password" autoComplete="false" type="password" placeholder="Password" />
                        <hr />
                        <Link to="/dashboard">
                            <button className="btn btn-primary btn-block">
                                Login
                            </button>
                        </Link>
                        <p className="forget-link">Did you <Link to="/restPassword">forget your password?</Link></p>
                    </div>
                </form>
            </div>
        );
    }
}

export default Login;