import React, {
    Component
} from 'react';

import './../../../styles/login.scss';
import {
    logo_splash_png
} from './../../../images';

import {Link} from 'react-router-dom';

class RestPassword extends Component {
    render() {
        return (
            <div className="main">
                <form className="splash-form">
                    <div className="logo-part">
                        <img src={logo_splash_png} alt="Liscgo" />
                    </div>
                    <div className="form-part">
                        <h3>Reset Password</h3>
                        <p>Enter the email address associated with your account, and we’ll email you a link to reset your password</p>
                        <input id="userName" type="email" placeholder="Email Address" />
                        <Link to="restPasswordDone" className="btn btn-primary btn-block">Reset Password</Link>
                    </div>
                </form>
            </div>
        );
    }
}

export default RestPassword;