import React, {
    Component
} from 'react';

import ContentPage from './../Containers/ContentPage';

class Dashboard extends Component {
    render() {
        return (
            <ContentPage pageTitle="Dashboard">
                    <div className="dashboard">
                        <div className="dashboard_item">
                            <div className="card stat">
                                <strong>123</strong> Organisations
                            </div>
                        </div>
                        <div className="dashboard_item">
                            <div className="card stat">
                                <strong>10</strong> Staff
                            </div>
                        </div>
                        <div className="dashboard_item">
                            <div className="card stat">
                                <strong>6</strong> Patients
                            </div>
                        </div>
                        <div className="dashboard_item">
                            <div className="card stat">
                                <strong>7</strong> Data Logs
                            </div>
                        </div>
                    </div>
            </ContentPage>
        );
    }
}

export default Dashboard;