import React, {
    Component
} from 'react';

import {
  Route,
  Switch
} from "react-router-dom";

import {
    Login,
    NewPassword,
    NewPasswordDone,
    RestPassword,
    RestPasswordDone,

    Dashboard,
    Organisation,
    OrganisationDetails,
    Patients,
    PatientsDetails,
    Profile,
    SettingTerms,
    Staff,

    Links
} from './views'

import './styles/main.scss';

class App extends Component {
    render() {
        return (
            <div>
              <Switch>
                <Route path="/" exact={true} component={Links}/>
                <Route path="/login" component={Login}/>
                <Route path="/restPassword" component={RestPassword}/>
                <Route path="/restPasswordDone" component={RestPasswordDone}/>
                <Route path="/newPassword" component={NewPassword}/>
                <Route path="/newPasswordDone" component={NewPasswordDone}/>
                <Route path="/dashboard" component={Dashboard}/>
                <Route path="/organisation" component={Organisation}/>
                <Route path="/organisationDetails" component={OrganisationDetails}/>
                <Route path="/patients" component={Patients}/>
                <Route path="/staff" component={Staff}/>
                <Route path="/settingTerms" component={SettingTerms}/>
                <Route path="/profile" component={Profile}/>
                <Route path="/patientDetails" component={PatientsDetails}/>
              </Switch>              
            </div>
        );
    }
}

export default App;