import React, {
    Component
} from 'react';

import './../../../styles/login.scss';
import {
    logo_splash_png,
    check_mark_svg
} from './../../../images';

import {Link} from 'react-router-dom';

class NewPasswordDone extends Component {
    render() {
        return (
            <div className="main">
                <form className="splash-form">
                    <div className="logo-part">
                        <img src={logo_splash_png} alt="Liscgo" />
                    </div>
                    <div className="form-part">
                        <h3>Reset Password Successful</h3>
                        <img src={check_mark_svg} alt="check" />
                        <Link to="/login" className="btn btn-primary btn-block">Back to Login</Link>                    
                    </div>
                </form>
            </div>
        );
    }
}

export default NewPasswordDone;