import React, {
    Component
} from 'react';
import {
    Link
} from 'react-router-dom';

import ContentPage from './../Containers/ContentPage';

import {
    button_plus_svg,
    search_svg
} from './../../images';

class Patients extends Component {
    render() {
        return (
            <ContentPage pageTitle="Patients">
                <div className="dashboard">
                    <div className="dashboard_item dashboard_item-full">
                        <div className="card">
                            <div className="main_part_header">
                                <div>
                                    <button className="btn btn-primary">Add Patient
                                        <img src={button_plus_svg} alt="" />
                                    </button>
                                </div>

                                <div className="search-box">
                                    <input type="text" className="search-input" />
                                    <img src={search_svg} className="search-icon" alt="" />
                                </div>
                            </div>

                            <div className="content">

                                <div className="table-responsive-md">
                                    <table className="table">
                                        <thead className="thead-light">
                                        <tr>
                                            <th scope="col">Date Registered</th>
                                            <th scope="col">Organisation Name</th>
                                            <th scope="col">First Name</th>
                                            <th scope="col">Last Name</th>
                                            <th scope="col"></th>
                                        </tr>
                                        </thead>
                                        <tbody>
                                        <tr>
                                            <td>dd/mm/yy</td>
                                            <td><Link to="/patientDetails">Nursing Home Limited</Link></td>
                                            <td>First Name</td>
                                            <td>Last Name</td>
                                            <td><Link to="/patientDetails" className="btn btn-primary">View</Link></td>
                                        </tr>
                                        <tr>
                                            <td>dd/mm/yy</td>
                                            <td><Link to="/patientDetails">Nursing Home Limited</Link></td>
                                            <td>First Name</td>
                                            <td>Last Name</td>
                                            <td><Link to="/patientDetails" className="btn btn-primary">View</Link></td>
                                        </tr>
                                        <tr>
                                            <td>dd/mm/yy</td>
                                            <td><Link to="/patientDetails">Nursing Home Limited</Link></td>
                                            <td>First Name</td>
                                            <td>Last Name</td>
                                            <td><Link to="/patientDetails" className="btn btn-primary">View</Link></td>
                                        </tr>
                                        </tbody>
                                    </table>
                                </div>
                                <nav>
                                    <ul className="pagination">
                                        <li className="page-item disabled">
                                            <a className="page-link" href="/" tabIndex="-1">Previous</a>
                                        </li>
                                        <li className="page-item page-number disabled active">
                                            <a className="page-link" href="/">1</a>
                                        </li>
                                        <li className="page-item page-number disabled">
                                            <a className="page-link" href="/">2</a>
                                        </li>
                                        <li className="page-item page-number disabled">
                                            <a className="page-link" href="/">3</a>
                                        </li>
                                        <li className="page-item disabled">
                                            <a className="page-link" href="/">Next</a>
                                        </li>
                                    </ul>
                                </nav>
                            </div>
                        </div>
                    </div>
                </div>
            </ContentPage>
        );
    }
}

export default Patients;